.signup{
    background-color: #fff;
    width: 70vw;
    min-height: 75vh;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
#signup-newuserimage{
    width: 316px;
    height: auto;
}
.signup-form-column{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.signup-h2{
    margin-bottom: 30px;
    background: linear-gradient(to right, #28ACAF, #47D534);
    background-clip: text;
    -webkit-text-fill-color: transparent;
}
.register-status{
    margin-left: 10px;
    margin-bottom: 20px;
    color: red;
}
input{
    color: #666666;
    height: 50px;
    width: 200px;
    font-size: 15px;
    line-height: 1.5;
    border-radius: 25px;
    padding: 0 30px 0 68px;
    outline: none;
	border: none;
    margin-bottom: 20px;
}
input[name='registerEmail']{
    background: #e6e6e6 url('../../Images/envelope-solid.svg') no-repeat scroll 25px 15px;
    background-size: 20px 20px;
    margin-right: 10px;
}
input[name='registerPassword']{
    background: #e6e6e6 url('../../Images/lock-solid.svg') no-repeat scroll 25px 15px;
    background-size: 20px 20px;
}
input[name='registerName']{
    background: #e6e6e6 url('../../Images/user-solid.svg') no-repeat scroll 25px 15px;
    background-size: 20px 20px;
    margin-right: 10px;
}
input[name='registerMobile']{
    background: #e6e6e6 url('../../Images/phone-solid.svg') no-repeat scroll 25px 15px;
    background-size: 20px 20px;
}
button[name='registerSubmit']:hover {
	cursor: pointer;
    background: #333333;
}
button[name='registerSubmit']{
    outline: none;
	border: none;
    font-size: 15px;
    line-height: 1.5;
    color: #fff;
    width: 60%;
    height: 50px;
    border-radius: 25px;
    background: #57b846;
    margin-top: 10px;
    font-weight: bold;
    margin-bottom: 30px;
}
.signup-form-row{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
}
.signup-create-account{
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
    margin-bottom: 10px;
}
.signup-create-account:hover{
    filter: invert(60%) sepia(17%) saturate(1534%) hue-rotate(65deg) brightness(101%) contrast(86%);
}
#signup-arrow-icon{
    width: 20px;
    height: 19px;
    align-self: flex-end;
    margin-right: 5px;
}
@media screen and (min-width: 481px) and (max-width: 865px) {
    .signup-form-row{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
    }

}
@media screen and (max-width: 480px) {
    .signup{
        min-height: 85vh;
        width: 80vw;
    }
    .signup-form-row{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
    }

}